import ApiService from "@/services/ApiService.js";

export const store = {
  state: {
    categories: [],
    selectedCategoryName: "",
    selectedCategoryBooks: []
  },
  fetchCategories() {
    ApiService.fetchCategories()
      .then(categories => (this.state.categories = categories))
      .catch(reason => {
        console.log("Error loading categories ", reason);
      });
  },
  getSelectedCategoryName(name) {
    this.state.selectedCategoryName = name;
  },
  fetchCategoryBooks() {
    ApiService.fetchCategoryBooks(this.state.selectedCategoryName)
        .then(selectedCategoryBooks => (this.state.selectedCategoryBooks = selectedCategoryBooks))
        .catch(reason => {
          console.log("Error loading categories books ", reason);
        });
  }
};
